    margin-bottom: 20px;
}

/* ============ Dashboard section =========== */

.dashboard-header{
    background-color: #0093FF;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    padding: 15px;
}

.dashboard-content{
    display:flex;
    flex-direction: row;
}
.dashboard-list-cont, .dashboard-list-cont-no-effect {
    background-color: #fff;
    cursor:pointer;
    min-height: 60px;
    height: auto;
    border-radius: 3px;
    padding: 20px;
    margin: 10px;
    width:inherit;
    border: solid thin #f3f3f3;
}

.dashboard-list-cont:hover {
    background-color: #f3f3f3 ;
}

.dashboard-list-cont:hover {
    -webkit-animation-name: mobile-menu-button-anim;
            animation-name: mobile-menu-button-anim;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    -webkit-animation-duration: 1.5s;
            animation-duration: 1.5s;
    background-color: #f3f3f3;
    box-shadow:10px 10px 25px gray;
}

.dashboard-list-cont > div {
    margin-bottom: 10px;
}

.dashboard-list-cont .title {
    font-weight: bold;
}
.dashboard-list-header{
    background-color: #efefef;
    color:#000;
    border-radius: 5px;
    font-size: 16pt;
}
.dashboard-list-delete {
    float:right;
    margin-right:auto;
    border: thin solid #f3f3f3 ;
    padding: 20px;
    margin-top: -10px;
    border-radius: 100px;
    color: #0059a3;
    background-color: #ffffff;
}
.dashboard-logout{
    cursor: pointer;
}
.dashboard-logout:hover{
   opacity: 0.5;
}
.dashboard-list-delete:hover, .dashboard-list-delete-small:hover {
    opacity: 0.7;
    -moz-opacity: 0.7;
}

/*
    The message box style here
 */

.alert{
    position:fixed;
    z-index:52000;
    left:15%;
    width:50%;
    top:5%;
}

.hidden{
    display:none;
}

.visible{
    display:inherit;
}

